import { Routes } from "@angular/router";
import {Router} from '@angular/router';
import { HeroComponent } from "./hero/hero.component";
import { TicketsComponent } from "./tickets/tickets.component";
import { TeamsComponent } from './teams/teams.component';
import { TicketDetailsComponent } from "./ticket-details/ticket-details.component";
import { ResultsComponent } from "./results/results.component";
import { ContactUsComponent } from "./contact-us/contact-us.component";
import { LoginComponent } from "./login/login.component";
import { SignUpComponent } from "./sign-up/sign-up.component";
import { FixturesComponent } from "./fixtures/fixtures.component";

export const appRoutes = [
{path: 'fibo', component: HeroComponent},
{path: 'fibo/tickets', component: TicketsComponent},
{path: 'fibo/teams', component: TeamsComponent},
{path: 'fibo/results',component: ResultsComponent},
{path: 'fibo/contact-us',component: ContactUsComponent},
{path: 'fibo/login',component: LoginComponent},
{path: 'fibo/sign-up',component: SignUpComponent},
{path: 'fibo/fixtures/:id',component: FixturesComponent},
{path: 'fibo/tickets/:id',component: TicketDetailsComponent},
{ path: '',redirectTo: 'fibo',pathMatch: 'full'},
]